import * as React from 'react';
import { animateScroll as scroll, scroller } from 'react-scroll';
import Typography from '@mui/material/Typography';
import defaultData from '../defaultData';
import DarumaIconText from '../svgs/daruma-icon-text';

const Navbar = () => {
  const duration = 500;
  // @TODO 動的に取得したかったがうまくいかなかったため決め打ち
  // const [navbarHeight, setNavbarHeight] = React.useState(67);
  const navbarHeight = 67;

  const scrollToTop = () => {
    scroll.scrollToTop({
      duration
    });
  };

  const scrollTo = element => {
    scroller.scrollTo(element, {
      duration,
      smooth: true,
      offset: navbarHeight * -1
    });
  };

  return (
    <nav className='navbar navbar-expand-lg fixed-top'>
      <div className='container my-2'>
        <a
          href='#top'
          className='navbar-brand'
          onClick={e => {
            e.preventDefault();
            scrollToTop();
          }}
          style={{
            cursor: 'pointer'
          }}
        >
          <DarumaIconText />
        </a>
        <button
          className='navbar-toggler'
          type='button'
          data-toggle='collapse'
          data-target='#navbarSupportedContent'
          aria-controls='navbarSupportedContent'
          aria-expanded='false'
          aria-label='Toggle navigation'
        >
          <span className='navbar-toggler-icon'></span>
        </button>

        <div className='collapse navbar-collapse' id='navbarSupportedContent'>
          <ul className='navbar-nav mr-auto'>
            {Object.keys(defaultData.sections)
              .filter(key => key !== 'contact')
              .map((key, i) => {
                const d = defaultData.sections[key];

                return (
                  <li className='nav-item mx-1 text-center' key={i}>
                    <a
                      href={`#${d.id}`}
                      className='nav-link mb-0 ml-3 text-black'
                      onClick={e => {
                        e.preventDefault();
                        scrollTo(d.id);
                      }}
                      style={{
                        cursor: 'pointer'
                      }}
                    >
                      <Typography
                        variant='h6'
                        className='mb-0'
                        style={{
                          letterSpacing: '0.1rem'
                        }}
                      >
                        {d.title}
                      </Typography>
                      <Typography variant='body2'>{d.title_ja}</Typography>
                    </a>
                  </li>
                );
              })}
            <li className='nav-item mx-1 d-flex align-items-center justify-content-center'>
              <a
                href='#contact'
                className='nav-link mb-0 ml-3 px-4 btn btn-primary rounded'
                onClick={e => {
                  e.preventDefault();
                  scrollTo('contact');
                }}
                style={{
                  cursor: 'pointer'
                }}
              >
                <i className='far fa-envelope fa-sm mr-2' />
                お問い合わせ
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
